.to-do-view {
	padding: 10px;
	height: calc(100% - 0px);
	overflow-y: hidden;
}

.to-do-view>.todo-table {
	width: 50%;
	height: 99%;
	border: 1px solid #ccc;
	border-radius: 10px;
	background-color: #fcfcfc;
	overflow-y: hidden;
}
.to-do-view>.todo-table>.table-row.header {
	background-color: #062a49;
	color: #fff;
	font-weight: 600;
	grid-template-columns: repeat(6, 16.3%);
}
.to-do-view>.todo-table>.table-row.header>div {
	border-right: 0px;
}
.to-do-view>.todo-table>.table-row.header>div.clickable:hover {
	background-color: #0b4475;
}

.todo-table>.tablerow-container>.table-row {
	grid-template-columns: repeat(6, 16.3%);
}