#task-schedule-container {
	height: 97.5vh;
}
.workflow-view-container {
	height: 97.5vh;
	width: 100%;
}

.workflow-view-actions {
	height: 40px;
	gap: 0px;
	display: flex;
	width: 25%;
}
.workflow-view-actions>.menu-button {
	height: 40px;
	line-height: 40px;
	font-size: 20px;
	border-bottom-left-radius: 0px;
	border-bottom-right-radius: 0px;
	filter: unset;
	background-color: #0d477a;
}
.workflow-view-actions>.menu-button:hover {
	background-color: #062a49;
}
.workflow-view-actions>.menu-button.selected {
	background-color: #062a49;
	font-weight: 600;
}
.workflow-view-actions>:nth-child(1) {
	border-top-right-radius: 0px;
	border-top-left-radius: 10px;
}
.workflow-view-actions>:last-child {
	border-top-left-radius: 0px;
	border-top-right-radius: 10px;
}

.workflow-content-card {
	height:calc(100% - 50px);
	width: calc(100% - 10px);
	max-width: unset;
	border-top-left-radius: 0px;
	border: 0px;
	overflow: unset;
}
.workflow-content-card>.card-menubar {
	position: relative;
	border-top-left-radius: 0px;
}
.workflow-content-card>.card-content {
	height: calc(100% - 40px);
	border-bottom-left-radius: 10px;
	border-bottom-right-radius: 10px;
	padding: 0px;
	border-left: 1px solid #ccc;
	border-right: 1px solid #ccc;
	border-bottom: 1px solid #ccc;
}
